html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
 
  text-align: center;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
// ==== countries.html
.countries__section {
  padding-top: 30px;
  padding-bottom: 30px;
}
.countries__container {
  width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
.search-form {
  text-align: center;
}
.country__input {
  padding: 10px;
  min-width: 350px;
}
.countries__list {
  margin-top: 0;
  margin-bottom: 0;
}


.country-item {
  font-size: 28px;
}
.card-title {
  font-size: 32px;
  color: #093131;
}
.card-img {
  width: 350px;
  padding-top: 10px;
}
.form-control {
  width: 250px;
  height: 36px;
  border: 2px solid #093131;
  border-radius: 2px;
  background-color: transparent;
  padding-left: 18px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.form-control::placeholder {
  font-weight: 600;
  font-size: 14px;
  color: #ffffff;
}
.input-name {
  margin-top: 80px;
  margin-bottom: 10px;
  font-size: 40px;
  color: #093131;
}
.card-wrapper {
  margin: 10px auto;
  padding: 15px;
  width: 400px;
  background-color: rgba(255, 255, 255, 0.397);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 0px 0px 4px 4px;
}
.card-item {
  font-weight: 600;
  color: #093131;
}
.list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}




.counties__item {
  font-size: 28px;
}

.counties__item:not(:last-child) {
  margin-bottom: 10px;
}

.country__name {
  text-align: center;
  font-size: 52px;
}

.country__content {
  display: flex;
}

.country__descr {
  margin-right: 30px;
}

.country__item {
  display: flex;
  align-items: center;
  font-size: 28px;
}

.country__info {
  font-weight: 600;
}

.country__languages {
  font-weight: 600;
}

.country__language {
  font-weight: 400;
}

.country__flag {
  display: block;
  width: 700px;
  height: auto;
}
